// The exported code uses Tailwind CSS. Install Tailwind CSS in your dev environment to ensure all styles work.
'use client';
import React, { useState, useEffect, useRef } from "react";
import React, { useState, useEffect } from "react";
import { Card } from '@/components/ui/card';
import { Heading } from '@/components/ui/heading';
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Collapsible, CollapsibleTrigger, CollapsibleContent } from "@/components/ui/collapsible";
import { Badge } from "@/components/ui/badge";
import { AiOutlineFolderOpen } from "react-icons/ai";
import { RxShare2 } from "react-icons/rx";
import { BsSearch } from "react-icons/bs";
import { ChevronRight, Folder, File } from "lucide-react";
import { useRouter } from 'next/navigation';
import ProtectedRoute from '@/components/ProtectedRoute'
import { useAuth } from '@/context/AuthContext';
import axios from 'axios';
import { Resizable } from 're-resizable';

const App: React.FC = () => {
  const router = useRouter();
  const { isAuthenticated, logout } = useAuth();
  const [userName, setUsername] = useState('');
  const [searchQuery, setSearchQuery] = useState("");
  const [uploadDialogOpen, setUploadDialogOpen] = useState(false);
  const [privateLibraryExpanded, setPrivateLibraryExpanded] = useState(false);
  const [publicLibraryExpanded, setPublicLibraryExpanded] = useState(false);
  const [documents, setDocuments] = useState<any[]>([]);
  const [aiMessage, setAiMessage] = useState("");
  const [selectedLibrary, setSelectedLibrary] = useState<{type: 'private' | 'group', groupId?: string}>({ type: 'private' });

  const [libraryTree, setLibraryTree] = useState<LibraryTree>({
    private: [],
    groups: [], // 支持多个课题组
  });

  type GroupLibrary = {
    groupId: string;      // 课题组唯一标识
    groupName: string;    // 课题组名称
    documents: Document[];// 该组下的文献
  };

  type LibraryTree = {
    private: Document[];
    groups: GroupLibrary[];
  };
  type Document = {
    id: number;
    title: string;
    author: string;
    tags: string[];
    date: string;
    // ...其他字段
  };

  useEffect(() => {
    if (!isAuthenticated) return;
    const token = localStorage.getItem('token');
    axios.get('http://localhost:8000/api/user/me', {
      headers: {
        Authorization: `Bearer ${token}`
      }
    })
   .then(res => {
      setUsername(res.data.username);
   })
    .catch(() => {
      logout();
      router.push('/login');
   });
  }, [isAuthenticated, logout, router]);

  // 初始化文献库
  const togglePrivateLibrary = () => {
    setPrivateLibraryExpanded(!privateLibraryExpanded);
  };

  const togglePublicLibrary = () => {
    setPublicLibraryExpanded(!publicLibraryExpanded);
  };

  const handleUpload = () => {
    setUploadDialogOpen(true);
  };

  const handleCloseDialog = () => {
    setUploadDialogOpen(false);
  };

  const handleConfirmUpload = () => {
    // 模拟添加文档
    const newDoc = {
      id: Date.now(),
      title: "深度学习在自然语言处理中的应用研究",
      author: "李四 等",
      tags: ["深度学习", "NLP"],
      date: "2023-12-20"
    };
    setDocuments([...documents, newDoc]);
    setUploadDialogOpen(false);
  };

  // 加入课题组
  const handleJoinGroup = async (newGroup: GroupLibrary) => {
    setLibraryTree(prev => ({
      ...prev,
      groups: [...prev.groups, newGroup]
    }));
  };

  // 退出课题组
  const handleLeaveGroup = (groupId: string) => {
    setLibraryTree(prev => ({
      ...prev,
      groups: prev.groups.filter(g => g.groupId !== groupId)
    }));
    // 如果当前选中的是被移除的组，切回私人库
    if (selectedLibrary.type === 'group' && selectedLibrary.groupId === groupId) {
      setSelectedLibrary({ type: 'private' });
    }
  };

  return (
    <div className="flex flex-col min-h-screen bg-white">
      {/* 顶部导航栏 */}
      <header className="flex items-center justify-between px-4 py-3 border-b border-gray-200">
        <div className="flex items-center">
          <div className="w-6 h-6 mr-2 text-black">
            <i className="fas fa-file-alt"></i>
          </div>
          <h1 className="text-lg font-medium text-black">AI+协同文献管理平台</h1>
        </div>
        
        <div className="flex-1 mx-6">
          <div className="relative">
            <Input
              type="text"
              placeholder="搜索文献..."
              className="w-full px-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-gray-400 text-sm"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
            <div className="absolute inset-y-0 right-0 flex items-center pr-3 pointer-events-none text-gray-400">
              <BsSearch className="w-4 h-4" />
            </div>
          </div>
        </div>
        
        <div className="flex items-center">
          {/* 用户名按钮，点击跳转到个人主页 */}
          <Button
            variant="ghost"
            className="mr-2"
            onClick={() => router.push('/home')}
            title="点击进入个人主页"
          >
            {userName}
          </Button>
          {/* 显示登录状态和退出按钮 */}
          {isAuthenticated && (
            <Button variant="outline" size="sm" onClick={logout}>
              退出登录
            </Button>
          )}
        </div>
      </header>

      <div className="flex flex-1">
        {/* 左侧导航栏 */}
       <aside className="w-64 border-r border-gray-200 bg-gray-50">
          <nav className="p-4 space-y-2">
            {/* 私人文献库 */}
            <Collapsible defaultOpen>
              <CollapsibleTrigger asChild>
                <button
                  className={`flex items-center w-full px-3 py-2 text-sm font-medium rounded hover:bg-gray-200 transition-all
                    [&[data-state=open]>.chevron]:rotate-90
                    ${selectedLibrary.type === 'private' ? 'bg-gray-200 text-black' : 'text-gray-700'}`}
                  onClick={() => setSelectedLibrary({ type: 'private' })}
                >
                  <ChevronRight className="chevron mr-2 transition-transform" />
                  <Folder className="w-4 h-4 mr-2" />
                  个人文献库
                </button>
              </CollapsibleTrigger>
              <CollapsibleContent>
                <div className="pl-6 mt-1 space-y-1">
                  {libraryTree.private.length === 0 ? (
                    <span className="text-xs text-gray-400">暂无文献</span>
                  ) : (
                    libraryTree.private.map(doc => (
                      <div key={doc.id} className="flex items-center px-2 py-1 rounded hover:bg-gray-100 text-sm text-gray-700 cursor-pointer">
                        <File className="w-3 h-3 mr-2 text-gray-400" />
                        {doc.title}
                      </div>
                    ))
                  )}
                </div>
              </CollapsibleContent>
            </Collapsible>

            {/* 动态课题组文献库 */}
            {libraryTree.groups.map(group => (
              <Collapsible key={group.groupId}>
                <CollapsibleTrigger asChild>
                  <button
                    className={`flex items-center w-full px-3 py-2 text-sm font-medium rounded hover:bg-gray-200 transition-all
                      [&[data-state=open]>.chevron]:rotate-90
                      ${selectedLibrary.type === 'group' && selectedLibrary.groupId === group.groupId ? 'bg-gray-200 text-black' : 'text-gray-700'}`}
                    onClick={() => setSelectedLibrary({ type: 'group', groupId: group.groupId })}
                  >
                    <ChevronRight className="chevron mr-2 transition-transform" />
                    <Folder className="w-4 h-4 mr-2" />
                    {group.groupName}
                  </button>
                </CollapsibleTrigger>
                <CollapsibleContent>
                  <div className="pl-6 mt-1 space-y-1">
                    {group.documents.length === 0 ? (
                      <span className="text-xs text-gray-400">暂无文献</span>
                    ) : (
                      group.documents.map(doc => (
                        <div key={doc.id} className="flex items-center px-2 py-1 rounded hover:bg-gray-100 text-sm text-gray-700 cursor-pointer">
                          <File className="w-3 h-3 mr-2 text-gray-400" />
                          {doc.title}
                        </div>
                      ))
                    )}
                  </div>
                </CollapsibleContent>
              </Collapsible>
            ))}
          </nav>
        </aside>

        {/* 主内容区 */}
        <main className="flex-1 p-6">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-xl font-semibold text-gray-800">
              {selectedLibrary.type === 'private'
                ? '我的文献'
                : libraryTree.groups.find(g => g.groupId === selectedLibrary.groupId)?.groupName || '课题组文献'}
            </h2>
            <Button 
              onClick={handleUpload} 
              className="!rounded-button whitespace-nowrap bg-black hover:bg-gray-800 text-white"
            >
              <RxShare2 className="w-4 h-4 mr-0" />
              上传文献
            </Button>
          </div>

          {/* 主内容区根据选中库动态渲染 */}
          {selectedLibrary.type === 'private' ? (
            libraryTree.private.length === 0 ? (
              <div className="flex flex-col items-center justify-center h-[500px]">
                <div className="w-24 h-24 mb-4 bg-[#FFF5F0] rounded-lg flex items-center justify-center">
                  <AiOutlineFolderOpen className="text-6xl text-gray-300" />
                </div>
                <p className="mb-2 text-lg font-medium text-gray-700">当前文献库还是空的</p>
                <p className="mb-6 text-sm text-gray-500">上传第一篇文献，开始研究之旅</p>
                <Button 
                  onClick={handleUpload} 
                  className="!rounded-button whitespace-nowrap bg-black hover:bg-gray-800 text-white"
                >
                  上传文献
                </Button>
              </div>
            ) : (
              <div className="space-y-4">
                {libraryTree.private.map(doc => (
                  <div 
                    key={doc.id}
                    className="p-4 border border-gray-200 rounded-lg hover:shadow-md transition-shadow"
                  >
                    <h3 className="mb-2 text-lg font-medium text-gray-800">{doc.title}</h3>
                    <div className="flex items-center mb-3">
                      <span className="text-sm text-gray-600">作者：{doc.author}</span>
                    </div>
                    <div className="flex items-center justify-between">
                      <div className="space-x-2">
                        {doc.tags.map((tag: string, index: number) => (
                          <Badge key={index} className="bg-gray-100 text-gray-700 hover:bg-gray-200">{tag}</Badge>
                        ))}
                      </div>
                      <span className="text-sm text-gray-500">{doc.date}</span>
                    </div>
                  </div>
                ))}
              </div>
            )
          ) : (() => {
            const group = libraryTree.groups.find(g => g.groupId === selectedLibrary.groupId);
            if (!group || group.documents.length === 0) {
              return (
                <div className="flex flex-col items-center justify-center h-[500px]">
                  <div className="w-24 h-24 mb-4 bg-[#FFF5F0] rounded-lg flex items-center justify-center">
                    <AiOutlineFolderOpen className="text-6xl text-gray-300" />
                  </div>
                  <p className="mb-2 text-lg font-medium text-gray-700">当前文献库还是空的</p>
                  <p className="mb-6 text-sm text-gray-500">上传第一篇文献，开始研究之旅</p>
                  <Button 
                    onClick={handleUpload} 
                    className="!rounded-button whitespace-nowrap bg-black hover:bg-gray-800 text-white"
                  >
                    上传文献
                  </Button>
                </div>
              );
            }
            return (
              <div className="space-y-4">
                {group.documents.map(doc => (
                  <div 
                    key={doc.id}
                    className="p-4 border border-gray-200 rounded-lg hover:shadow-md transition-shadow"
                  >
                    <h3 className="mb-2 text-lg font-medium text-gray-800">{doc.title}</h3>
                    <div className="flex items-center mb-3">
                      <span className="text-sm text-gray-600">作者：{doc.author}</span>
                    </div>
                    <div className="flex items-center justify-between">
                      <div className="space-x-2">
                        {doc.tags.map((tag: string, index: number) => (
                          <Badge key={index} className="bg-gray-100 text-gray-700 hover:bg-gray-200">{tag}</Badge>
                        ))}
                      </div>
                      <span className="text-sm text-gray-500">{doc.date}</span>
                    </div>
                  </div>
                ))}
              </div>
            );
          })()}
          {/* AI研究助手 */}
          <Resizable
            defaultSize={{
              width: '100%',
              height: 320, // 初始高度更大
            }}
            minHeight={200}
            maxHeight={600}
            enable={{
              top: false, right: false, bottom: true, left: false,
              topRight: false, bottomRight: false, bottomLeft: false, topLeft: false
            }}
            className="w-full border-t border-gray-200 bg-white shadow-lg"
          >
            <div className="p-4 max-w-7xl mx-auto h-full flex flex-col">
              <div className="flex items-center mb-4">
                <div className="w-8 h-8 rounded-full bg-gray-100 flex items-center justify-center mr-3">
                  <i className="fas fa-robot text-gray-600"></i>
                </div>
                <h3 className="font-medium text-gray-800">AI研究助手</h3>
              </div>
              <div className="mb-4 p-3 bg-gray-50 rounded-lg flex-1 overflow-auto">
                <div className="flex items-start">
                  <div className="w-6 h-6 rounded-full bg-gray-100 flex items-center justify-center mr-2">
                    <i className="fas fa-robot text-xs text-gray-600"></i>
                  </div>
                  <p className="text-sm text-gray-700">
                    您好！我是您的AI研究助手，我可以帮助您分析文献、回答问题，让您的研究更高效。请问有什么我可以帮您的吗？
                  </p>
                </div>
              </div>
              <div className="flex">
                <Input
                  type="text"
                  placeholder="请输入您的问题..."
                  className="flex-1 border-gray-300 focus:ring-gray-400 text-sm"
                  value={aiMessage}
                  onChange={(e) => setAiMessage(e.target.value)}
                />
                <Button 
                  className="ml-2 !rounded-button whitespace-nowrap bg-black hover:bg-gray-800 text-white"
                >
                  发送 <i className="fas fa-paper-plane ml-1"></i>
                </Button>
              </div>
            </div>
          </Resizable>
        </main>
      </div>

      {/* 上传文献对话框 */}
      <Dialog open={uploadDialogOpen} onOpenChange={setUploadDialogOpen}>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle>上传文献</DialogTitle>
          </DialogHeader>
          
          <Tabs defaultValue="file" className="w-full">
            <TabsList className="grid w-full grid-cols-2">
              <TabsTrigger value="file">文件上传</TabsTrigger>
              <TabsTrigger value="web">网页链接</TabsTrigger>
            </TabsList>
            
            <TabsContent value="file" className="p-4">
              <div className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center">
                <div className="mx-auto w-12 h-12 mb-4 text-gray-400">
                  <RxShare2 className="w-12 h-12" />
                </div>
                <p className="mb-2 text-sm font-medium text-gray-700">拖拽文件到此处或点击上传</p>
                <p className="text-xs text-gray-500 mb-4">支持 PDF、Word 格式</p>
                <Button className="!rounded-button whitespace-nowrap bg-black hover:bg-gray-800 text-white text-sm">
                  选择文件
                </Button>
              </div>
            </TabsContent>
            
            <TabsContent value="web" className="p-4">
              <div className="space-y-4">
                <Input 
                  type="text" 
                  placeholder="输入文献网页链接" 
                  className="w-full"
                />
                <p className="text-xs text-gray-500">支持知网、万方、Google Scholar 等学术网站链接</p>
              </div>
            </TabsContent>
          </Tabs>
          
          <DialogFooter className="sm:justify-end">
            <Button
              variant="outline"
              onClick={handleCloseDialog}
              className="!rounded-button whitespace-nowrap"
            >
              取消
            </Button>
            <Button
              onClick={handleConfirmUpload}
              className="!rounded-button whitespace-nowrap bg-black hover:bg-gray-800 text-white"
            >
              确认上传
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default App;
